import React from "react";
import { Line } from "react-chartjs-2";
import {
    Chart as ChartJS,
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend,
} from "chart.js";

//  Chart.js 
ChartJS.register(
    CategoryScale,
    LinearScale,
    PointElement,
    LineElement,
    Title,
    Tooltip,
    Legend
);

const TemperatureChart = ({ data }) => {

    // 

    // X 
    const labels = data.map((item) => {
        const dateObj = new Date(item.date);
        return `${dateObj.getMonth() + 1}/${dateObj.getDate()}`;
    });

    const maxTemps = data.map((item) => Number(item.maxTemp)); // 
    const minTemps = data.map((item) => Number(item.minTemp)); // 

    const chartData = {
        labels,
        datasets: [
            {
                label: " (C)",
                data: maxTemps,
                borderColor: "red",
                backgroundColor: "rgba(255, 99, 132, 0.5)",
                tension: 0.3, // 
            },
            {
                label: " (C)",
                data: minTemps,
                borderColor: "blue",
                backgroundColor: "rgba(54, 162, 235, 0.5)",
                tension: 0.3,
            },
        ],
    };

    const options = {
        responsive: true,
        maintainAspectRatio: false,  //div
        plugins: {
            legend: {
                display: false,
                position: "top",
                labels: {
                    color: "rgba(255, 255, 255,0.3)", // 
                    font: {
                        size: 14
                    }
                }
            },
            title: {
                display: false,
                text: " 7 ",
                color: "rgba(255, 255, 255)", // 
                font: {
                    size: 18,
                    weight: "bold",
                }
            }
        },
        scales: {
            x: {
                ticks: {
                    color: "rgba(255, 255, 255,0.3)", // X 
                    font: {
                        size: 16,
                        weight: "bold",
                    }
                }
            },
            y: {
                beginAtZero: false,
                ticks: {
                    color: "rgba(255, 255, 255,0.3)", // Y 
                    font: {
                        size: 16,
                        weight: "bold",
                    },
                }
            }
        }
    };

    // 
    const chartContainerStyle = {
        backgroundColor: "rgba(75, 115, 227,0)",
        padding: "10px",
        borderRadius: "10px",
        width: "100%",  //  div 
        height: "400px"//  canvas 
    };

    return (
        <div style={chartContainerStyle}>
            <Line data={chartData} options={options} />
        </div>
    );
};

export default TemperatureChart;