import { React, useState, useEffect } from "react";
import { fetchCityList, fetchWeather } from "../utils/cwbApi";
import { fetchEnvData } from "../utils/epaApi";

const CityOption = ({ isShow, setSelectedCity, setWeatherData, setEnvData }) => {
    const [showCityOptions, setShowCityOptions] = useState(isShow);  // 
    const [cities, setCities] = useState([]);  // 

    //  API 
    useEffect(() => {
        const fetchCities = async () => {
            try {
                // 
                const locations = await fetchCityList();
                setCities(locations);
            } catch (error) {
                console.error(":", error);
            }
        };

        fetchCities();
    }, []);

    // 
    const handleCitySelect = async (cityName) => {
        setSelectedCity(cityName);
        setShowCityOptions(false);
        try {
            // 
            const data = await fetchWeather(cityName);
            setWeatherData(data);  // 

            // 
            const envData = await fetchEnvData(cityName);
            setEnvData(envData);  // 

        } catch (error) {
            console.error(":", error);
        }
    };

    //  showCityOptions  className
    useEffect(() => {
        setShowCityOptions(isShow);
    }, [isShow]);

    return (
        <div className={`city-option ${showCityOptions ? "show" : "hidden"}`}>
            <ul className="list-none col gap-5">
                {cities.length > 0 ? (
                    cities.map((cityName, index) => (
                        <li key={index} className="options" onClick={() => handleCitySelect(cityName)}>
                            <h1 className="text-xl">{cityName}</h1>
                        </li>
                    ))
                ) : (
                    <li><p>......</p></li> // 
                )}
            </ul>
        </div>
    );
}

export default CityOption;